$(function () {
    $("#owl-example").owlCarousel({
        navigation: true, // Show next and prev buttons
        autoPlay: 3500, //Set AutoPlay to 3 seconds
        slideSpeed: 1000,
        paginationSpeed: 1000,
        singleItem: true
    });

    // let test = 'my name is ';
    //
    // alert(test);

    $('#demo-url').on('focus', function(){
        var fn = $(this);
        if(fn.val() == '') fn.val('http://');
    });

    if ($(window).width() <= 900){
        $('#how-it-works-link').removeClass('open-popup-link').attr('href', 'https://www.youtube.com/v/-0hajliHa3U')
            .attr('target', '_blank')
            .attr('rel', 'noopener');
    }

    $('.open-popup-link').magnificPopup({
        type: 'inline',
        midClick: true // Allow opening popup on middle mouse click. Always set it to true if you don't provide alternative source in href.
    });
});